import { RendererCanvas } from '../../../lib'

export class Solution {
    public divHost: HTMLElement;
    public renderer: CanvasNavigation;
    public div: HTMLElement;
    constructor(id: string) {
        this.divHost = document.getElementById(id);

        // Center align
        this.divHost.style.display = 'flex';
        this.divHost.style.alignItems = 'center';

        this.div = document.createElement('div');
        this.div.style.marginLeft = 'auto';
        this.div.style.marginRight = 'auto';
        this.div.style.backgroundColor = '#555';
        this.div.style.width = '1000px';
        this.div.style.height = '600px';
        this.divHost.appendChild(this.div);

        // TODO
        // You code goes here for one time operation

        this.renderer = new CanvasNavigation(this);

        // console.log(this.renderer);
        // !! you should execute it for rendering
        // this.start();
    }
    public destroy() {
        this.renderer.destroy();
        while (this.divHost.lastElementChild) {
            this.divHost.removeChild(this.divHost.lastElementChild);
        }
    }
}


export class CanvasNavigation {
    solution: Solution;

    canvas: HTMLCanvasElement;
    statusRendering: Vector;
    statusTarget: Vector = new Vector(200, 200, 4);

    needInterpolation: boolean = false;

    img: HTMLImageElement;
    ctx: CanvasRenderingContext2D;

    speedInterpolation: number = 0.1;
    easingInterpolation: boolean = true;

    constructor(solution: Solution) {
        this.solution = solution;
        // https://gist.github.com/dzhang123/2a3a611b3d75a45a3f41
        // refer: https://codepen.io/NJStudio/pen/poZoEgY?editors=0010
        this.canvas = document.createElement('canvas');
        this.canvas.style.background = '#555';
        // this.canvas.style.width = '100%';
        // this.canvas.style.height = '100%';
        this.canvas.width = 1000;
        this.canvas.height = 600;
        solution.div.appendChild(this.canvas);
        this.ctx = this.canvas.getContext('2d')


        this.statusRendering = new Vector(0, 0, 1)
        let statusPre = new Vector(0, 0, 1)
        let status = new Vector(0, 0, 1)
        let isDrag = false

        this.canvas.addEventListener('mousedown', (e) => {
            const { offsetX, offsetY } = e
            status.x = offsetX
            status.y = offsetY
            isDrag = true
            // if (status.z > 2) isDrag = true
        });
        this.canvas.addEventListener('mouseup', (e) => {
            if (!isDrag) return
            const { offsetX, offsetY } = e
            const dx = offsetX - status.x
            const dy = offsetY - status.y
            statusPre.x += dx
            statusPre.y += dy
            isDrag = false
        });
        this.canvas.addEventListener('wheel', (e) => {
            const { offsetX, offsetY, deltaY } = e

            const oy = deltaY < 0 ? 1 : -1
            status.z += (0.2 * oy)

            if (status.z < 0.2) {
                status.z = 0.2
            } else if (status.z > 2) {
                status.z = 2
            }

            const { x: zx, y: zy } = zoomOffset(this.canvas, status.z, { x: offsetX, y: offsetY }, statusPre)
            statusPre.x = zx;
            statusPre.y = zy;
            statusPre.z = status.z;

            this.statusRendering.x = zx;
            this.statusRendering.y = zy;
            this.statusRendering.z = status.z;

            // draw(ctx, image, this.statusRendering);
        });
        this.canvas.addEventListener('mousemove', (e) => {
            if (!isDrag) return
            const { offsetX, offsetY } = e
            const x = statusPre.x + (offsetX - status.x)
            const y = statusPre.y + (offsetY - status.y)
            const { x: zx, y: zy } = applyBoundaries(this.canvas, statusPre.z, { x, y })

            console.log('dddd');

            this.statusRendering.x = zx;
            this.statusRendering.y = zy;
            this.statusRendering.z = statusPre.z;
            // draw(ctx, image, this.statusRendering)
        });


        this.render(this.ctx);

    }
    public destroy() {

    }
    public render(ctx: CanvasRenderingContext2D) {
        requestAnimationFrame(() => { this.render(ctx); });

        ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);

        ctx.save();
        ctx.translate(this.statusRendering.x, this.statusRendering.y);
        
        // console.log(this.statusRendering.x, this.statusRendering.y)
        // ctx.scale(this.statusRendering.z, this.statusRendering.z)

        // ctx.restore();

        ctx.beginPath();
        ctx.arc(100, 100, 100, 0, Math.PI * 2.0);
        ctx.closePath();
        ctx.fill();

        ctx.restore()
    }
}
export class Vector {
    static add(v0: Vector, v1: Vector) {
        return new Vector(v0.x + v1.x, v0.y + v1.y, v0.z + v1.z);
    }
    static subtract(v0: Vector, v1: Vector) {
        return new Vector(v0.x - v1.x, v0.y - v1.y, v0.z - v1.z);
    }
    public static distance(v0: Vector, v1: Vector): number {
        return Math.sqrt((v0.x - v1.x) * (v0.x - v1.x) + (v0.y - v1.y) * (v0.y - v1.y) + (v0.z - v1.z) * (v0.z - v1.z));
    }
    public static deepCopy(v: Vector): Vector {
        return new Vector(v.x, v.y, v.z);
    }
    public x: number
    public y: number
    public z: number
    constructor(x: number, y: number, z: number) {
        this.x = x;
        this.y = y;
        this.z = z;
    }
    public length(): number {
        return Math.sqrt(this.x * this.x + this.y * this.y + this.z * this.z);
    }
    public normalize() {
        let len: number = Math.sqrt(this.x * this.x + this.y * this.y + this.z * this.z);
        this.x /= len;
        this.y /= len;
        this.z /= len;
    }
};
export const zoomOffset = (canvas, scale, { x, y }, statusPre) => {
    const isZooming = scale !== 1
    const newX = isZooming ? (x + Math.abs(statusPre.x)) / statusPre.z : x
    const newY = isZooming ? (y + Math.abs(statusPre.y)) / statusPre.z : y
    const dx = newX - (newX * scale) + (x - newX)
    const dy = newY - (newY * scale) + (y - newY)
    return { ...applyBoundaries(canvas, scale, { x: dx, y: dy }) }
};
export const applyBoundaries = (canvas, scale, { x, y }) => {
    const scaleMargin = 1 - scale;
    const maxOffsetX = scaleMargin * canvas.width;
    const maxOffsetY = scaleMargin * canvas.height;
    if (x > 0) {
        x = 0
    } else if (x < maxOffsetX) {
        x = maxOffsetX;
    }
    if (y > 0) {
        y = 0
    } else if (y < maxOffsetY) {
        y = maxOffsetY;
    }
    return { x, y }
};